import React from "react"
import {
  Collapse,
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink
} from "reactstrap"
import classnames from "classnames"
import { Eye, Code, ChevronDown } from "react-feather"



class AssetAccordion extends React.Component {
  state = {
    activeTab: "1",
    collapseID: "",
    status: "Closed",
    collapseItems: this.props.collapseItems
  }

  toggleTab = tab => {
    if (this.state.activeTab !== tab) {
      this.setState({ activeTab: tab })
    }
  }

  toggleCollapse = collapseID => {
    this.setState(prevState => ({
      collapseID: prevState.collapseID !== collapseID ? collapseID : ""
    }))
  }

  onEntered = id => {
    if (id === this.state.collapseID) this.setState({ status: "Opened" })
  }
  onEntering = id => {
    if (id === this.state.collapseID) this.setState({ status: "Opening..." })
  }

  onExited = id => {
    if (id === this.state.collapseID) this.setState({ status: "Closed" })
  }

  onExiting = id => {
    if (id === this.state.collapseID) this.setState({ status: "Closing..." })
  }

  render() {
    const accordionMarginItems = this.state.collapseItems > 0 ? 
    
    this.state.collapseItems.map(collapseItem => {
      return (
        <div className="collapse-margin" key={collapseItem.id}>
          <Card
            onClick={() => this.toggleCollapse(collapseItem.id)}
            className={classnames({
              "collapse-collapsed":
                this.state.status === "Closed" &&
                this.state.collapseID === collapseItem.id,
              "collapse-shown":
                this.state.status === "Opened" &&
                this.state.collapseID === collapseItem.id,
              closing:
                this.state.status === "Closing..." &&
                this.state.collapseID === collapseItem.id,
              opening:
                this.state.status === "Opening..." &&
                this.state.collapseID === collapseItem.id
            })}
          >
            <CardHeader>
              <CardTitle className="lead collapse-title collapsed">
                {collapseItem.title}
              </CardTitle>
              <ChevronDown size={15} className="collapse-icon" />
            </CardHeader>
            <Collapse
              isOpen={collapseItem.id === this.state.collapseID}
              onEntering={() => this.onEntering(collapseItem.id)}
              onEntered={() => this.onEntered(collapseItem.id)}
              onExiting={() => this.onExiting(collapseItem.id)}
              onExited={() => this.onExited(collapseItem.id)}
            >
              <CardBody>{collapseItem.content}</CardBody>
            </Collapse>
          </Card>
        </div>
      )
      
    }) : 
    <span style={{fontSize:'12px', marginTop: '-10px'}}>No Asset Found</span>

    return (
      <React.Fragment>
        <Card>
          <CardHeader>
            <CardTitle>{this.props.heading}</CardTitle>
            <div className="views">
              <Nav tabs>
                <NavItem>
                  <NavLink
                    className={classnames({
                      active: this.state.activeTab === "1"
                    })}
                    onClick={this.props.show}
                    // onClick={() => {
                    //   this.toggleTab("1")
                    //   this.props.show()
                    // }}
                  >
                    <Eye size={15} onClick={this.props.show}/>
                  </NavLink>
                </NavItem>
                {/* <NavItem>
                  <NavLink
                    className={classnames({
                      active: this.state.activeTab === "2"
                    })}
                    onClick={() => {
                      this.toggleTab("2")
                    }}
                  >
                    <Code size={15} />
                  </NavLink>
                </NavItem> */}
              </Nav>
            </div>
          </CardHeader>
          <CardBody>
            <p>
              {this.props.description}
            </p>
            <TabContent activeTab={this.state.activeTab}>
              <TabPane tabId="1">
                <div className="vx-collapse">{accordionMarginItems}</div>
              </TabPane>
              <TabPane className="component-code" tabId="2">
                hey
              </TabPane>
            </TabContent>
          </CardBody>
        </Card>
      </React.Fragment>
    )
  }
}
export default AssetAccordion
